.section {
    padding-top: 72px;
    padding-bottom: 72px;
    &__headline {
        @extend .h2 !optional;
        text-align: center; }
    &__subheadline {
        text-align: center;
        max-width: 600px;
        margin: auto auto; }
    &.services {
        padding-top: 0;
        background: #f6f6f6;
        .description {
            padding: 24px 24px 0 24px; } } }

@media screen and (min-width: $br--d) {
    .section.services {
        display: flex;
        justify-content: row;
        align-items: center;
        padding: 0;
        .media {
            flex: 0 0 auto;
            img {
                display: block; } }
        .description {
            max-width: 960px;
            margin-right: auto;
            padding-left: 72px; } } }
